@import url(https://fonts.googleapis.com/css2?family=Pattaya&family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #5958a6;
  font-family: 'Poppins', sans-serif;
}
html {
  font-size: 62.5%;
}
h1 {
  font-size: 2.6rem;
}
li,button,label,input,p {
  font-size: 2rem;
}
h2{
  font-size: 4.8rem;
}
h3 {
  font-size: 3rem;
  font-weight: normal;
}
h4,h5 {
  font-size: 2.8rem;
}


@media screen and (max-width: 950px){
  html {
      font-size: 40%;
  }
  nav {
      text-align: center;
  }
  .logo_container{
      padding: 1rem;
      margin-bottom: 1rem;
  }

}

@media screen and (max-width: 550px){
  .container{
    flex-direction: column;
  }
  /* .right img{
    width: 60%;
    height: 60%;
  } */
  .benefits__head {
    margin-top: 10rem;
  }
}
header{
  background-color: #F0F9FF;
}
#menu {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 10;
  border-bottom: 3px solid rgba(89, 88, 166, 0.3);
}
nav{
  display: flex;
  width: 90%;
  margin: auto;
  padding: 2rem;
  min-height: 10vh;
  align-items: center;
  flex-wrap: wrap;
}
.logo__container {
  flex: 2 1 40px;
  font-family: 'Pattaya', sans-serif;
  font-weight: 400;
}
.menu__logo {
  width: 10rem;
}
nav ul {
  display: flex;
  flex : 1 1 40rem;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.menu__link{
  text-decoration: none;
}

.menu__active{
  background-color: #fe55bf;
  color: #FFFFFF;
  padding: 1rem 3rem;
  border-radius: 1.5rem 2.5rem 0 2.5rem;
  transition: .5s ease-in-out;
}
footer {
    background-color: #F0F9FF;
  }
  .footerWrapper{
    display: flex;
    padding: 2rem;
    width: 90%;
    margin: auto;
    align-items: center;
    min-height: 10vh;
    flex-wrap: wrap;
  }
  .footerText{
    flex: 1 1 40rem;
    padding: 2rem;
  }
  .footerP{
    font-size: 100%;
    color: #fe55bf;
  }
  .footerWrapper ul {
    display: flex;
    list-style: none;
    flex: 1 1 40rem;
    justify-content: space-between;
    align-items: center;
    background-color: inherit;
  }

  @media screen and (max-width: 550px){
    .footerText{
      text-align: center;
    }
  }
.card {
    min-height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 2rem 5rem;
    flex: 1 1 35rem;
    padding: 1rem;
    /* border-bottom-right-radius: 10rem; */
    /* color:  #fe55bf; */
    /* border-top-right-radius: 7.5rem;
    border-bottom-left-radius: 7.5rem; */
    border-radius: 7.5rem 7.5rem 0 7.5rem;
    box-shadow: 0px 10px 10px rgba(89, 88, 166, 0.2), 0px 20px 20px rgba(254, 85, 191, 0.1);
  }
  .card:hover{
      background-color: #F0FFFF;
      transition: background 1s ease-in-out;
  }
  .card__imageContainer{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card__detailsContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .servicesIcon{
    height: 8rem;
  }

.container{
    min-height: 90vh;
    display: flex;
    flex-wrap: wrap;
    background-color: #F0F9FF;
  }
  .leftColumn {
    flex: 1 1 50%;
    display: flex;
    flex-direction: column;
    align-items: unset;
    padding-left: 10rem;
    padding-right: 5rem;
    justify-content: center;
  
  }
  .leftColumn__buttonContainer{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: flex-start;
  }
  button {
    padding: 1rem 2rem;
    margin-top: 2rem;
    margin-right: 2rem;
    border: none;
    cursor: pointer;
  }
  .buttonContainer__button2{
    background-color: #fe55bf;
    color: #FFFFFF;
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 2.5rem;
    border-bottom-left-radius: 2.5rem;
    box-shadow: 0px 10px 10px rgba(89, 88, 166, 0.3), 0px 20px 20px rgba(254, 85, 191, 0.4);
  }
  .buttonContainer__button2:hover{
    background-color: #e35bb0;
    font-size: 2.03rem;
  }
  .buttonContainer__button1{
    background-color: #5958a6;
    color: #FFFFFF;
    border-bottom-right-radius: 1.5rem;
    border-top-right-radius: 2.5rem;
    border-bottom-left-radius: 2.5rem;
    box-shadow: 0px 10px 10px  rgba(254, 85, 191, 0.3), 0px 20px 20px rgba(89, 88, 166, 0.4) ;
  }
  .left p {
    background-color: inherit;
    color:  #fe55bf;
  }
  .right{
    flex: 1 1 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .right img{
    width: 90%;
    height: 90%;
  }  
  #services{
    min-height: 100vh;
    background-color: #F0F9FF;
    padding: 3rem;
  }
  .benefits__head {
    padding: 3rem;
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
  }
  .benefits__head h3{
    padding: 1rem;
    color:  #fe55bf;
  }
    .cardParent{
    display: flex;
    flex-wrap: wrap;
  }
  .footer__icon{
      height: 3rem;
      width: 3rem;
  }
.holidayCard{
    max-height: 30rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 2rem 5rem;
    flex:1 1 auto;
    padding-bottom: 1rem;
    border-radius: 1rem;
    box-shadow: 0px 10px 10px rgba(89, 88, 166, 0.2), 0px 20px 20px rgba(254, 85, 191, 0.1);
}
.imageContainer{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    box-shadow: 0px 10px 10px rgba(89, 88, 166, 0.3), 0px 20px 20px rgba(254, 85, 191, 0.1);
}
.holidayImage{
    height: 15rem;
    width: 20rem;
}
.detailsContainer{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.holidayCard .detailsContainer p{
    color: #fe55bf;
}
.holidayCard .detailsContainer h5{
    font-weight: 400;
    font-size: 1.2rem;
}
.holidayContainer{
    min-height: 90vh;
    background-color: #F0F9FF;
    display: flex;
    flex-direction: column;
}
.holidayContainer h1{
    flex: 1 1 auto;
    margin: auto;
    padding: 2rem;
}
.holidayCards{
    flex: 1 1 40rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
#contact{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    min-height: 90vh;
    flex-direction: column;
}
#contact h2{
    margin: 3rem;
}
.contact__formContainer{
    box-shadow: 0px 10px 10px rgba(89, 88, 166, 0.2), 0px 20px 20px rgba(254, 85, 191, 0.1);
}
form {
    display: flex;
    flex-direction: column;
    justify-content:  center;
    align-items: center;

}

.formContainer_name,.formContainer_email{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 1rem;
}
label{
    margin-bottom: 1rem;
}
.submitButton{
    background-color: #fe55bf;
    color: #FFFFFF;
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 2.5rem;
    border-bottom-left-radius: 2.5rem;
    box-shadow: 0px 10px 10px rgba(89, 88, 166, 0.3), 0px 20px 20px rgba(254, 85, 191, 0.4);
    margin-bottom: 3rem;
}
